html, body, #root, #app {
    height: 100%
}

/*grid style*/
.cell-empty {
    fill: rgb(222, 222, 222);
    stroke-width: 1px;
    stroke: rgb(0, 0, 0)
}

.cell-block {
    fill: rgb(0, 0, 0);
    stroke-width: 1px;
    stroke: rgb(0, 0, 0);
}

.shape {
    fill: rgb(255, 197, 197);
    stroke-width: 2px;
    stroke: rgb(0, 0, 0)
}

.mark {
    fill: rgb(255, 0, 0);
    stroke-width: 1px;
    stroke: rgb(0, 0, 0)
}